<template>
    <div class="add-leave">
        <div class="content">
            <el-input
                    type="textarea"
                    :placeholder=" $L('请输入留言内容...')"
                    v-model="textarea"
                    maxlength="500"
                    rows="10">
            </el-input>
            <p class="tips">
                {{ $L('温馨提示') }}：{{ textarea  ?  textarea.length : '0' }}/{{ $L('500字以内') }}
            </p>
            <div class="dialog-footer">
                <el-button class="btn" type="primary" @click="submitFunc" size="large">{{ $L('提交留言') }}</el-button>
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        name: "addLeave",
        data () {
            return {
                textarea:''
            }
        },
        methods : {
            clearFunc() {
                this.textarea = '';
            },
            submitFunc() {
                const _self = this;
                // AddLeaveMsg
                _self.dialogVisible = true;
                if (_self.textarea !== '') {
                    //关闭对话框并开启动画
                    _self.$api('AddLeaveMsg', {
                        content: _self.textarea
                    }, function (reData) {
                        if (reData.flag === 1) {
                            _self.$message({
                                type: 'success',
                                message: _self.$L('留言成功!')
                            });
                            _self.$router.push({name:'leaveList'});
                            _self.textarea = '';
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.dialogVisible = true;
                            }).catch(action => {
                                _self.$router.push({name: 'leaveList'});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.dialogVisible = true;
                        }).catch(action => {
                            _self.$router.push({name: 'leaveList'});
                        });
                    });
                } else {
                    _self.$message({
                        type: 'info',
                        message: _self.$L('未输入留言信息!')
                    });
                }

            },
        }
    }
</script>

<style lang="scss" scoped>
   .add-leave{
       position: absolute;
       top: 0;
       left: 0;
       width: 100%;
       height: 100%;
       background: #eff1f5;
       padding: 10px 0;
       .content{
           width: inherit;
           background: #fff;
           /deep/ .el-textarea__inner{
               border: none;
           }
           .tips{
               padding: 10px;
               color: #999;
               border-top: 1px solid #eee;
           }
           .dialog-footer{
                padding: 10px;
               .btn{
                   width: 100%;
               }
           }
       }

   }
</style>
<style scoped>
    .el-message-box,.el-message{
        width: 94% !important;
    }
</style>